	module.exports = {"data":{"post":{"html":"<p>At work we're a waiting on the result of a court order <em><a href=\"http://dict.leo.org/?search=injunction\">(einstweilige Verfügung)</a></em>. I thought there was a song about waiting on an injunction, but I guess I was thinking of Losing My Religion by R.E.M. So we'll have to go with <a href=\"http://www.geocities.com/regitorico/shouldi.htm\">The Clash</a>: </p>\n<p>\"Should I stay or should I go now?<br />\nShould I stay or should I go now?<br />\nIf I go there will be trouble<br />\nAn' if I stay it will be double<br />\nSo you gotta let me know<br />\nShould I stay or should I go?\"</p>","fields":{"title":"Should I Stay Or Should I Go?","date":"16 Aug 2004","url":"https://papascott.de/2004-08-16-should-i-stay-or-should-i-go/"}}},"pathContext":{"slug":"/2004-08-16-should-i-stay-or-should-i-go/","prev":{"fields":{"title":"Naturalized","date":"15 Aug 2004","slug":"/2004-08-15-naturalized/"}},"next":{"fields":{"title":"Lowercase Reform","date":"17 Aug 2004","slug":"/2004-08-17-lowercase-reform/"}}}}

